/* 自定义 Publications 页面按钮样式 */
.publications a.btn.btn-sm {
 border: 1px solid #ccc;
 border-radius: 4px;
 padding: 2px 6px;
 margin-right: 4px;
 font-size: 0.85rem;
 background-color: #fff;
 color: #000;
 transition: all 0.2s ease-in-out;
}

.publications a.btn.btn-sm:hover {
 background-color: #f8f9fa;
 border-color: #999;
 text-decoration: none;
}
